import React from "react"
import Link from "gatsby-link"
import styled from "styled-components"
import Helmet from "react-helmet"
import Fade from "react-reveal/Fade"

import {
  TINY_SHADOW,
  TINY_BKG,
  SECONDARY_TEXT
} from "../../components/consts/colors.js"

import "../../css/index.css"
import "../../css/typography.css"
import "../../css/Hide.css"
import SectionContainer from "../../components/SectionContainer"
import SectionContainerBackground from "../../components/SectionContainerBackground"
import SectionHeaderContent from "../../components/SectionHeaderContent"
import SectionContent from "../../components/SectionContent"
import {
  TinyButton,
  TinyButtonAlt,
  TinyButtonWhite
} from "../../components/Button.js"
import Icon from "../../components/Icons"
import SectionImages from "../../components/SectionImages"
import SectionImage from "../../components/SectionImage"
import FAQHeader from "../../components/FAQHeader"
import demo01 from "../../assets/demo-01.png"
import demo02 from "../../assets/demo-02.png"
import demo03 from "../../assets/demo-03.png"
import demo04 from "../../assets/demo-04.png"
import demo05 from "../../assets/demo-05.png"
import demo06 from "../../assets/demo-06.png"

import withScreensize from "../../components/withScreensize"
import ScreensizeProvider from "../../components/ScreensizeProvider"

const pageData = {
  FAQHeadline: "TinyMerch is a simple, embeddable store for your website",
  FAQCopy: (
    <p>
      Sell your own unique, branded merchandise, custom content, or original
      artwork without any of the hassles and headaches that come with running a
      traditional online store. With TinyMerch there's no inventory or
      fulfillment to worry about. You simply design your products, set a price,
      and start making money.
    </p>
  ),
  FAQLink: "#",
  FAQwhatIsCTA: "Get Started!"
}

const NextQ = styled.div`
  margin: 10px 0px;
  a {
    display: flex;
    transition: all 200ms ease;
    :hover {
      transform: translateX(3px);
    }
    div {
      padding-left: 10px;
      position: relative;
      top: 2px;
    }
  }
`

const WhatIs = withScreensize(props => {
  const isMobile = props.screensize === "phones"
  const isTablet = props.screensize === "thatUglyInBetweenSize"
  const isSmall = props.screensize === "devWithConsoleOpen"
  const isWidescreen = props.screensize === "widescreenFools"

  return (
    <div>
      <Helmet title="TinyMerch | FAQ – What is TinyMerch" />
      <FAQHeader />
      <SectionContainer positionContent="center">
        <SectionContent
          justify="center"
          color
          height={
            isMobile
              ? "300px"
              : isTablet ? "400px" : isWidescreen ? "500px" : "500px"
          }
          width={
            isMobile ? "95%" : isTablet ? "80%" : isWidescreen ? "40%" : "50%"
          }>
          <Fade left delay={50} duration={200}>
            <h2>{pageData.FAQHeadline}</h2>
          </Fade>
          <Fade left delay={75} duration={200}>
            <p style={{ paddingTop: "25px" }}>{pageData.FAQCopy}</p>
          </Fade>
          <Fade left delay={100} duration={200}>
            <NextQ>
              <Link to="/faq/designing-products">
                🖌️ Designing Products
                <Icon name="forward" color="#6079F2" />
              </Link>
            </NextQ>
          </Fade>
          {/* <a href={pageData.FAQLink}>
            <TinyButton>{pageData.FAQwhatIsCTA}</TinyButton>
          </a> */}
        </SectionContent>
        <SectionImages
          width={
            isMobile ? "100%" : isTablet ? "100%" : isWidescreen ? "60%" : "50%"
          }
          height={
            isMobile
              ? "200px"
              : isTablet ? "300px" : isWidescreen ? "600px" : "600px"
          }>
          <Fade right delay={200} duration={300}>
            <SectionImage
              src={demo01}
              ImgWidth={
                isMobile
                  ? "100px"
                  : isTablet ? "150px" : isWidescreen ? "250px" : "225px"
              }
              positionTB={
                isMobile
                  ? "10px"
                  : isTablet ? "50px" : isWidescreen ? "80px" : "100px"
              }
              positionLR={
                isMobile ? "0%" : isTablet ? "0%" : isWidescreen ? "20%" : "10%"
              }
            />
          </Fade>
          <Fade right delay={225} duration={300}>
            <SectionImage
              src={demo02}
              ImgWidth={
                isMobile
                  ? "100px"
                  : isTablet ? "150px" : isWidescreen ? "250px" : "225px"
              }
              positionTB={
                isMobile
                  ? "10px"
                  : isTablet ? "50px" : isWidescreen ? "80px" : "100px"
              }
              positionLR={
                isMobile
                  ? "0%"
                  : isTablet ? "0%" : isWidescreen ? "-30%" : "-40%"
              }
            />
          </Fade>
          <Fade right delay={250} duration={300}>
            <SectionImage
              src={demo03}
              ImgWidth={
                isMobile
                  ? "100px"
                  : isTablet ? "150px" : isWidescreen ? "250px" : "225px"
              }
              positionTB={
                isMobile
                  ? "10px"
                  : isTablet ? "50px" : isWidescreen ? "80px" : "100px"
              }
              positionLR={
                isMobile
                  ? "0%"
                  : isTablet ? "0%" : isWidescreen ? "-80%" : "-90%"
              }
            />
          </Fade>
          <Fade right delay={275} duration={300}>
            <SectionImage
              src={demo04}
              ImgWidth={
                isMobile
                  ? "100px"
                  : isTablet ? "150px" : isWidescreen ? "250px" : "225px"
              }
              positionTB={
                isMobile
                  ? "10px"
                  : isTablet ? "50px" : isWidescreen ? "80px" : "100px"
              }
              positionLR={
                isMobile
                  ? "0%"
                  : isTablet ? "0%" : isWidescreen ? "-130%" : "-140%"
              }
            />
          </Fade>
          <Fade right delay={300} duration={300}>
            <SectionImage
              src={demo05}
              ImgWidth={
                isMobile
                  ? "100px"
                  : isTablet ? "150px" : isWidescreen ? "250px" : "225px"
              }
              positionTB={
                isMobile
                  ? "10px"
                  : isTablet ? "50px" : isWidescreen ? "80px" : "100px"
              }
              positionLR={
                isMobile
                  ? "0%"
                  : isTablet ? "0%" : isWidescreen ? "-180%" : "-190%"
              }
            />
          </Fade>
          <Fade right delay={325} duration={300}>
            <SectionImage
              src={demo06}
              ImgWidth={
                isMobile
                  ? "100px"
                  : isTablet ? "150px" : isWidescreen ? "250px" : "225px"
              }
              positionTB={
                isMobile
                  ? "10px"
                  : isTablet ? "50px" : isWidescreen ? "80px" : "100px"
              }
              positionLR={
                isMobile ? "0%" : isTablet ? "0%" : isWidescreen ? "30%" : "20%"
              }
            />
          </Fade>
        </SectionImages>
      </SectionContainer>
    </div>
  )
})

export default props => (
  <ScreensizeProvider>
    <WhatIs {...props} />
  </ScreensizeProvider>
)



